<template>
  <li class="song" @click="setCurrentSongId(songItem.id)" :class="{ranking:isShowRanking}">
    <div class="left">{{(index+1)>9?(index+1):"0"+(index+1)}}</div>
    <dl class="content">
      <dt>{{songItem.name}}</dt>
      <dd v-if="songItem.song">
        <span v-if="(songItem.song.hMusic)&& isShowQuality">SQ</span>
        {{songItem.song.artists[0].name}}-{{songItem.song.album.name}}
      </dd>
      <dd v-if="songItem.ar">
        <span v-if="(songItem.h)&& isShowQuality">SQ</span>
        {{songItem.ar[0].name}}-{{songItem.al.name}}
      </dd>
      <dd v-if="songItem.album">
        <span v-if="(songItem.h)&& isShowQuality">SQ</span>
        {{songItem.artists[0].name}}-{{songItem.album.name}}
      </dd>
    </dl>
    <div class="right">
      <span></span>
    </div>
  </li>
</template>

<script>
export default {
  props: ["index", "songItem", "xxx", "isShowQuality", "isShowRanking"],
  methods: {
    setCurrentSongId(id) {
      this.$emit("translate-currentid", {
        songId: id,
        showFullscreen: window.location.hash.match("search") ? true : false
      });
      // let list = JSON.parse(window.localStorage.getItem("xxx"));
      window.localStorage.setItem(
        "list",
        JSON.stringify({
          data: this.xxx
        })
      );
    }
  }
};
</script>

<style lang="less">
li.song {
  display: flex;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  &.ranking:nth-child(1) .left {
    color: red;
  }
  &.ranking:nth-child(2) .left {
    color: red;
  }
  &.ranking:nth-child(3) .left {
    color: red;
  }

  dl.content {
    flex: 1;
    dt {
      line-height: 30px;
    }
    dd {
      line-height: 20px;
      font-size: 12px;
      color: #888;
      span {
        color: red;
        font-size: 10px;
        display: inline-block;
        line-height: 10px;
        border: 1px solid red;
      }
    }
  }
  .left {
    width: 50px;
    height: 50px;
    line-height: 50px;
    text-align: center;
  }
  .left {
    //   font-size: 24px;
    font-weight: 400;
    color: #888;
  }
  .right {
    margin-right: 10px;
    line-height: 50px;
    text-align: center;
    span {
      display: inline-block;
      width: 22px;
      height: 22px;
      background-image: url("https://s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28c3767992ca4bb6d4887c74880=");
      background-size: 166px 97px;
      background-position: -24px 0;
      background-repeat: no-repeat;
      vertical-align: middle;
    }
  }
}
</style>