<template>
  <div class="recommend">
    <HomeTitle>推荐歌单</HomeTitle>
    <ul class="list">
      <SongListCard v-for="(item, index) in personalized" :key="index" :item="item" :index="index"></SongListCard>
    </ul>
    <HomeTitle>最新音乐</HomeTitle>
    <ul class="newsong">
      <SongListItem
        v-for="(item, index) in newsong"
        :key="index"
        :song-item="item"
        :index="index"
        :is-show-quality="true"
        :is-show-ranking="false"
        @translate-currentid="$emit('translate-currentid',$event)"
        :xxx="newsong"
      ></SongListItem>
    </ul>
  </div>
</template>

<script>
import HomeTitle from "@/components/HomeTitle.vue";
import SongListCard from "@/components/SongListCard.vue";
import SongListItem from "@/components/SongListItem.vue";

export default {
  name: "HomeRecommend",
  components: {
    HomeTitle,
    SongListCard,
    SongListItem
  },
  data() {
    return {
      personalized: [],
      newsong: []
    };
  },
  methods: {
    getPersonalized() {
      let personalized = JSON.parse(
        window.localStorage.getItem("personalized")
      );
      if (personalized && Date.now() < personalized.time) {
        // 从本地拿数据 本地有数据 且时间还没过10分钟
        this.personalized = personalized.data;
      } else {
        this.axios
          .get("http://music.kele8.cn/personalized?limit=6")
          .then(response => {
            ({ result: this.personalized } = response.data);
            // console.log( response.data);
            // 存储到本地存储 附加信息 过期时间  拿数据之后十分钟之内
            window.localStorage.setItem(
              "personalized",
              JSON.stringify({
                time: Date.now() + 10 * 60 * 1000,
                data: this.personalized
              })
            );
          });
      }
    },
    getNewsong() {
      let newsong = JSON.parse(window.localStorage.getItem("newsong"));
      if (newsong && Date.now() < newsong.time) {
        this.newsong = newsong.data;
      } else {
        this.axios
          .get("http://music.kele8.cn/personalized/newsong")
          .then(response => {
            ({ result: this.newsong } = response.data);
            // console.log( response.data);
            window.localStorage.setItem(
              "newsong",
              JSON.stringify({
                time: Date.now() + 10 * 60 * 1000,
                data: this.newsong
              })
            );
          });
      }
    }
  },
  created() {
    this.getPersonalized();
    this.getNewsong();
  }
};
</script>

<style lang="less">
ul.list {
  display: flex;
  flex-wrap: wrap;
  li.card {
    flex: 1 0 30%;
    &:nth-child(3n + 2) {
      margin: 0 5px;
    }
  }
}
</style>