<template>
  <div class="player" v-if="currentSongDetail" :class="{paused:isPaused}">
    <audio :src="currentSongUrl" controls autoplay></audio>
    <div class="play-bar" @click="isShowFullscreenPlayer=true">
      <img :src="currentSongDetail.al.picUrl" alt />
      <h3>{{currentSongDetail.name}}</h3>
      <div class="state">
        <span class="fa fa-play progress" @click.stop="pausedAudio" v-if="isPaused"></span>
        <span class="fa fa-pause progress" @click.stop="pausedAudio" v-else></span>
        <canvas width="42" height="42" @click.stop="pausedAudio"></canvas>
      </div>
    </div>
    <template v-if="isShowFullscreenPlayer">
      <div class="player-fullscreen" :class="{paused:isPaused}">
        <div class="playerBg" :style="{'background-image':`url(${currentSongDetail.al.picUrl})`}"></div>
        <i class="fa fa-chevron-left back" aria-hidden="true" @click="isShowFullscreenPlayer=false"></i>
        <div class="content-lyric" v-if="isSeelyric">
          <div class="songdescription">
            <h5>
              <span>{{currentSongDetail.name}}-{{currentSongDetail.ar[0].name}}</span>
            </h5>
            <div class="lyric">
              <ul
                class="songwords"
                @scroll="stopPevent"
                v-on:touchstart="clear"
                v-on:touchmove="handle"
                v-on:touchend="set"
              >
                <li v-for="(item, index) in lyricArr" :key="index">{{item.text}}</li>
              </ul>
              <p @click="isSeelyric=false">切换控制页面</p>
            </div>
          </div>
        </div>
        <div class="content" v-else>
          <div class="cdimage" @click.stop="pausedAudio">
            <img :src="currentSongDetail.al.picUrl" alt />
            <span class="progress" v-if="isPaused"></span>
          </div>
          <div class="songdescription">
            <h5>
              <span>{{currentSongDetail.name}}-{{currentSongDetail.ar[0].name}}</span>
            </h5>
            <div class="lyric">
              <ul
                class="songwords"
                @scroll="stopPevent"
                v-on:touchstart="clear"
                v-on:touchmove="handle"
                v-on:touchend="set"
              >
                <li v-for="(item, index) in lyricArr" :key="index">{{item.text}}</li>
              </ul>
              <p @click="isSeelyric=true">查看完整歌词</p>
            </div>
          </div>
          <div class="controler">
            <div class="line">
              <span>{{curtime}}</span>
              <label>
                <div class="progress-bar"></div>
                <input
                  type="range"
                  value="0"
                  min="0"
                  max="100"
                  step="1"
                  v-on:input.prevent="changeProgress"
                />
              </label>
              <span>{{totaltime}}</span>
            </div>

            <div class="controls">
              <span>
                <i class="fa fa-backward" @click="$emit('prevsong')"></i>
              </span>
              <span>
                <i class="fa fa-play" v-if="isPaused" @click="pausedAudio"></i>
                <i class="fa fa-pause" v-else @click="pausedAudio"></i>
              </span>
              <span>
                <i class="fa fa-forward" @click="$emit('nextsong')"></i>
              </span>
            </div>
          </div>
        </div>
      </div>
    </template>
    <img class="loading" v-if="loading" src="../assets/sample.gif" alt />
  </div>
</template>

<script>
export default {
  props: ["currentSongId", "showFullscreen"],
  data() {
    return {
      currentSongDetail: null,
      isPaused: false,
      isShowFullscreenPlayer: this.showFullscreen || false,
      currentTime: 0,
      animateId: null,
      songwords: {},
      loading: false,
      lyricArr: [],
      currentIndex: 0,
      curtime: 0,
      totaltime: 0,
      isSeelyric: false
    };
  },
  computed: {
    currentSongUrl() {
      return (
        "https://music.163.com/song/media/outer/url?id=" +
        this.currentSongId +
        ".mp3"
      );
    }
  },
  methods: {
    getSongDetail() {
      this.loading = true;
      this.axios
        .get("http://music.kele8.cn/song/detail", {
          params: {
            ids: this.currentSongId
          }
        })
        .then(response => {
          //   window.console.log(response);
          this.currentSongDetail = response.data.songs[0];
          this.loading = false;
        });
    },
    getSongWords() {
      this.loading = true;
      this.axios
        .get("http://music.kele8.cn/lyric", {
          params: {
            id: this.currentSongId
          }
        })
        .then(response => {
          //   window.console.log(response.data.lrc);
          this.songwords = response.data.lrc;
          this.loading = false;
          this.jiexi();
        });
    },
    pausedAudio() {
      let audio = this.$el.querySelector("audio");
      if (this.isPaused) {
        audio.play();
      } else {
        audio.pause();
      }
      this.isPaused = audio.paused;
    },
    drawCircleProgress() {
      let audio = this.$el.querySelector("audio");
      let canvas = this.$el.querySelector("canvas");
      let ctx = canvas.getContext("2d");

      let id = window.setInterval(() => {
        // window.console.log("dd");
        this.mainsetLyricScroll();
        // this.minorsetLyricScroll();
        this.currentTime = audio.currentTime * 1000;
        ctx.beginPath();
        ctx.lineWidth = 2;
        ctx.arc(19, 19, 18, 0, 2 * Math.PI);
        ctx.closePath();
        ctx.strokeStyle = "#ccc";
        ctx.stroke();

        ctx.beginPath();
        ctx.lineWidth = 2;
        ctx.arc(
          19,
          19,
          18,
          -0.5 * Math.PI,
          2 * Math.PI * (this.currentTime / this.currentSongDetail.dt) -
            0.5 * Math.PI
        );
        ctx.strokeStyle = "lightslategray";
        ctx.stroke();
      }, 500);
      return id;
    },
    setProgress() {
      let progress = this.$el.querySelector(".progress-bar");
      if (progress) {
        progress.style.width =
          (this.currentTime / this.currentSongDetail.dt) * 100 + "%";
      }
    },
    changeProgress(e) {
      //   console.log(e.target.value);
      let progress = this.$el.querySelector(".progress-bar");
      let audio = this.$el.querySelector("audio");

      progress.style.width = e.target.value + "%";
      audio.currentTime =
        ((e.target.value / 100) * this.currentSongDetail.dt) / 1000;
    },
    jiexi() {
      var arr = [];
      this.songwords.lyric.split("\n").forEach((element, index) => {
        // console.log(element);
        var t = element.slice(1, 10);
        // console.log(t);

        arr[index] = {
          time: Number(t.slice(0, 2)) * 60 + Number(t.slice(3)),
          text: element.slice(11).trim()
        };
      });
      arr.pop();
      // console.log(arr);
      arr.forEach(element => {
        if (element.text == "") {
          element.text = "------------";
        }
      });
      this.lyricArr = arr;
      // return arr;
    },
    // 歌词自动滚动函数
    mainsetLyricScroll() {
      if (this.isShowFullscreenPlayer) {
        let audio = this.$el.querySelector("audio");
        let lis = this.$el.querySelectorAll("ul.songwords li");
        let songwordsBox = this.$el.querySelector("ul.songwords");

        // let id = window.setInterval(() => {
        this.currentTime = audio.currentTime;
        // 查找当前歌词索引
        for (let i = 0; i < this.lyricArr.length; i++) {
          if (this.lyricArr[i].time >= audio.currentTime) {
            this.currentIndex = i;
            break;
          }
        }
        // 设置当前歌词高亮
        lis.forEach((element, index) => {
          // console.log(index, this.currentIndex);
          if (index == this.currentIndex) {
            if (index < 1) {
              lis[0].classList.add("active");
            } else if (index == this.lyricArr.length - 1) {
              lis[index].classList.add("active");
            } else {
              lis[index - 1].classList.add("active");
              // songwordsBox.style.marginTop = -index * 20 + 50 + "px";
            }
          } else {
            lis[index].classList.remove("active");
          }
        });
        // 判断当前歌词展示的位置 如果大于高度的一半让其垂直居中
        console.log(lis[this.currentIndex].offsetTop - songwordsBox.offsetTop);
        var disparity =
          lis[this.currentIndex].offsetTop - songwordsBox.offsetTop;
        if (disparity > 210) {
          songwordsBox.scrollTo({
            top: disparity - 210,
            behavior: "smooth"
          });
        }
        // }, 200);
        // return id;
      }
    },
    minorsetLyricScroll() {
      if (this.isShowFullscreenPlayer) {
        let audio = this.$el.querySelector("audio");
        let lis = this.$el.querySelectorAll("ul.songwords li");
        let songwordsBox = this.$el.querySelector("ul.songwords");

        // let id = window.setInterval(() => {
        this.currentTime = audio.currentTime;
        // 查找当前歌词索引
        for (let i = 0; i < this.lyricArr.length; i++) {
          if (this.lyricArr[i].time >= audio.currentTime) {
            this.currentIndex = i;
            break;
          }
        }
        // 设置当前歌词高亮
        lis.forEach((element, index) => {
          // console.log(index, this.currentIndex);
          if (index == this.currentIndex) {
            if (index < 1) {
              lis[0].classList.add("active");
            } else if (index == this.lyricArr.length - 1) {
              lis[index].classList.add("active");
            } else {
              lis[index - 1].classList.add("active");
              // songwordsBox.style.marginTop = -index * 20 + 50 + "px";
            }
          } else {
            lis[index].classList.remove("active");
          }
        });
        // 判断当前歌词展示的位置 如果大于高度的一半让其垂直居中
        console.log(lis[this.currentIndex].offsetTop - songwordsBox.offsetTop);
        var disparity =
          lis[this.currentIndex].offsetTop - songwordsBox.offsetTop;
        if (disparity > 60) {
          songwordsBox.scrollTo({
            top: disparity - 60,
            behavior: "smooth"
          });
        }
        // }, 200);
        // return id;
      }
    },
    stopPevent(e) {
      e.preventDefault();
    },
    clear() {
      window.clearInterval(this.animateId);
    },
    handle(e) {
      let songwordsBox = this.$el.querySelector("ul.songwords");
      // console.log(e);
      this.currentIndex = Math.round(songwordsBox.scrollTop / 20 + 1);
      console.log(this.currentIndex);
    },
    set() {
      let audio = this.$el.querySelector("audio");
      this.animateId = this.drawCircleProgress();
      audio.currentTime = this.lyricArr[this.currentIndex].time;
      this.jiexi();
    },
    calculateTime(time) {
      var curtime = parseInt(time / 1000);
      var m = parseInt(curtime / 60);
      var s = parseInt(curtime % 60);
      var ftime;

      if (curtime < 10) {
        ftime = "0:0" + curtime;
      }
      if (curtime > 10 && curtime < 60) {
        ftime = "0:" + curtime;
      }
      if (curtime > 60 && s < 10) {
        ftime = m + ":0" + s;
      }
      if (curtime > 60 && s > 10) {
        ftime = m + ":" + s;
      }
      return ftime;
    }
  },
  created() {
    // 第一次进来就拿数据
    this.getSongDetail();
    this.getSongWords();
  },

  watch: {
    currentSongId() {
      // 每次id变化也拿数据
      this.getSongDetail();
      this.getSongWords();
      this.isShowFullscreenPlayer = this.showFullscreen;
      this.isPaused = false;
    },
    isPaused() {
      if (this.isPaused) {
        window.clearInterval(this.animateId);
      } else {
        window.clearInterval(this.animateId);
        this.animateId = this.drawCircleProgress();
      }
    },
    currentSongDetail() {
      this.$nextTick(function() {
        window.clearInterval(this.animateId);
        this.animateId = this.drawCircleProgress();
      });
    },
    currentTime() {
      this.setProgress();
      // console.log(this.currentTime);
      this.curtime = this.calculateTime(this.currentTime);
      this.totaltime = this.calculateTime(this.currentSongDetail.dt);
    }
  }
};
</script>

<style lang="less" >
.player {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  audio {
    height: 60px;
  }
  .play-bar {
    display: flex;
    border-top: 1px solid lightgray;
    background: rgba(255, 255, 255, 0.95);
    padding: 7px 10px;
    line-height: 36px;
    img {
      width: 36px;
      height: 36px;
      border-radius: 50%;
      animation: myrolling 5s linear infinite;
    }
    h3 {
      flex: 1;
      margin: 0 10px;
    }
    .state {
      width: 38px;
      height: 38px;
      position: relative;
      .progress {
        width: 40px;
        height: 40px;
        // background: yellow;
        line-height: 38px;
        text-align: center;
      }
      canvas {
        position: absolute;
        left: 0;
        top: 0;
      }
    }
  }
  &.paused {
    .play-bar {
      img {
        animation-play-state: paused;
      }
    }
  }
  .player-fullscreen {
    width: 100vw;
    height: 100vh;
    position: relative;
    &::after {
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      background: rgba(0, 0, 0, 0.5);
    }
    .playerBg {
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      background-color: #161824;
      background-position: 50%;
      background-repeat: no-repeat;
      background-size: auto 100%;
      transform: scale(1.5);
      filter: blur(3px);
      overflow: hidden;
    }
    i.back {
      color: #eee;
      font-size: 32px;
      position: fixed;
      left: 5%;
      top: 5%;
      z-index: 999;
    }
    .content {
      position: absolute;
      top: 10%;
      z-index: 2;
      width: 100%;
      height: 80%;
      text-align: center;
      .cdimage {
        width: 300px;
        height: 300px;
        margin: 0 auto;
        background-image: url("https://s3.music.126.net/mobile-new/img/disc.png?d3bdd1080a72129346aa0b4b4964b75f=");
        background-size: cover;
        background-position: center;
        position: relative;
        img {
          width: 60%;
          border-radius: 50%;
          margin-top: 20%;
          animation: myrolling 10s linear infinite;
        }
        span.progress {
          display: inline-block;
          width: 56px;
          height: 56px;
          line-height: 56px;
          font-size: 36px;
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
          background: url("data:image/png;base64,");
          background-size: cover;
        }
        &:after {
          content: "";
          width: 96px;
          height: 137px;
          position: absolute;
          top: -70px;
          left: 133px;
          background-image: url("https://s3.music.126.net/mobile-new/img/needle-ip6.png?be4ebbeb6befadfcae75ce174e7db862=");
          background-size: contain;
          z-index: 5;
        }
      }
      .songdescription {
        color: hsla(0, 0%, 100%, 0.6);
        margin-top: 30px;
        h5 {
          font-size: 16px;
          color: white;
          margin-bottom: 15px;
        }
        .lyric {
          position: relative;
          box-sizing: border-box;
          // overflow-y: scroll;
          // height: 100px;
          // ::before {
          //   content: "";
          //   display: block;
          //   width: 100%;
          //   position: absolute;
          //   top: 50%;
          //   left: 0;
          //   border-top: 1px dashed white;
          // }

          ul.songwords {
            width: 100%;
            transition: all 0.3s;
            overflow-y: scroll;
            height: 100px;
            padding: 30px 0;
            li {
              font-size: 14px;
              line-height: 20px;
              &.active {
                color: white;
              }
            }
          }
          p {
            font-size: 18px;
            color: lightskyblue;
          }
        }
      }
      .controler {
        margin-top: 20px;
        .line {
          display: flex;
          span {
            display: inline-block;
            color: hsla(0, 0%, 100%, 0.6);
            font-size: 14px;
            width: 30px;
            height: 10px;
            line-height: 10px;
            text-align: center;
            margin: 0 5px;
          }
          label {
            background: rgba(0, 0, 0, 0.3);
            display: block;
            height: 10px;
            width: 75%;
            float: 1;
            position: relative;
            overflow: hidden;
            margin: 0 auto;
            .progress-bar {
              position: absolute;
              left: 0;
              top: 0;
              // width: 50%;
              background: #f55c5c;
              &::after {
                content: "";
                display: block;
                width: 10px;
                height: 10px;
                background: white;
                border-radius: 50%;
                float: right;
                margin-right: -5px;
              }
            }
            input {
              height: 5px;
              line-height: 5px;
              position: absolute;
              left: 0;
              top: 0;
              opacity: 0;
              width: 100%;
              height: 100%;
            }
          }
        }

        .controls {
          margin-top: 10px;
          display: flex;
          
          span {
            flex: 1;
            text-align: center;
            // padding: 20px 0;
            position: relative;
            line-height: 30px;
            color: white;
            i.fa-play,
            i.fa-pause {
              position: absolute;
              left: 0;
              top: 0;
              right: 0;
              margin: auto;

              &::before {
                line-height: 30px;
              }
            }
          }
        }
      }
    }
    .content-lyric {
      position: absolute;
      top: 5%;
      z-index: 2;
      width: 100%;
      height: 80%;
      text-align: center;
      .songdescription {
        color: hsla(0, 0%, 100%, 0.6);
        margin-top: 30px;
        h5 {
          font-size: 18px;
          color: white;
          margin-bottom: 15px;
        }
        .lyric {
          position: relative;
          box-sizing: border-box;
          overflow-y: scroll;
          // height: 100px;
          ::before {
            content: "";
            display: block;
            width: 100%;
            position: absolute;
            top: 41%;
            left: 0;
            border-top: 1px dashed white;
          }

          ul.songwords {
            width: 100%;
            transition: all 0.3s;
            overflow-y: scroll;
            height: 70vh;
            padding: 190px 0;
            li {
              font-size: 16px;
              line-height: 20px;
              &.active {
                color: white;
              }
            }
          }
          p {
            font-size: 18px;
            margin-top: 20px;
            color: lightskyblue;
          }
        }
      }
    }
    &.paused {
      .cdimage {
        img {
          animation-play-state: paused;
        }
      }
    }
  }
}
img.loading {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 99;
}
@keyframes myrolling {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>