<template>
  <div class="searchinout">
    <form>
      <div class="inputcover">
        <i class="fa fa-search" aria-hidden="true"></i>
        <input
          type="text"
          v-model.trim="inputText"
          placeholder="搜索歌曲、歌手、专辑"
          @focus="$emit('checkList')"
          @keyup.enter="$emit('getMultimatch',inputText)"
        />
        <i class="fa fa-times" aria-hidden="true" v-if="inputText" @click="inputText=''"></i>
      </div>
      <h3 v-if="inputText">搜索：{{inputText}}</h3>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      inputText: null
    };
  },
  watch: {
    inputText(value) {
      this.$emit("translate-search-text", value);
    }
  }
};
</script>

<style lang="less">
form {
  padding: 10px 15px;
  .inputcover {
    width: 100%;
    height: 30px;
    border-radius: 30px;
    background: #ebecec;
    box-sizing: border-box;
    display: flex;
    line-height: 30px;
    i {
      width: 30px;
      height: 30px;
      display: inline-block;
      text-align: center;
      color: gray;
      line-height: 30px;
    }
    input {
      flex: 1;
      height: 30px;
      background: transparent;
      font-size: 14px;
      color: #333;
      outline: none;
      border: none;
      font-size: 14px;
    }
  }
}
h3 {
  // margin-top: 10px;
  font-size: 15px;
  line-height: 40px;
  color: #507daf;
}
</style>