<template>
  <div class="hot">
    <div class="hottop">
      <div class="hottitle">
        <div class="hoticon"></div>
        <p>更新日期:12月30日</p>
      </div>
    </div>
    <ul>
      <SongListItem
        v-for="(item, index) in hotsong.tracks"
        :key="index"
        :song-item="item"
        :index="index"
        :is-show-quality="true"
        :is-show-ranking="true"
        @translate-currentid="$emit('translate-currentid',$event)"
        :xxx="hotsong.tracks"
      ></SongListItem>
    </ul>
  </div>
</template>

<script>
import SongListItem from "@/components/SongListItem.vue";

export default {
  data() {
    return {
      hotsong: {}
    };
  },
  components: {
    SongListItem
  },
  methods: {
    getHotSong() {
      this.axios.get("http://music.kele8.cn/top/list?idx=1").then(response => {
        ({ playlist: this.hotsong } = response.data);
        console.log(response.data);
      });
    }
  },
  created() {
    this.getHotSong();
  }
};
</script>

<style lang="less">
.hottop {
  background: url("https://s3.music.126.net/mobile-new/img/hot_music_bg_3x.jpg?2bfefd3ba37eaffbd66b7d108b9de26a=");
  position: relative;
  padding-top: 38.9%;
  overflow: hidden;
  background-size: contain;

  .hottitle {
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    padding-left: 20px;
    box-sizing: border-box;
    .hoticon {
      background: url("https://s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28c3767992ca4bb6d4887c74880=")
        no-repeat;
      background-size: 166px 97px;
      width: 142px;
      height: 67px;
      background-position: -24px -30px;
    }
    p {
      margin-top: 10px;
