<template>
  <div class="search">
    <SearchInput
      @translate-search-text="translateText($event)"
      @checkList="Check"
      @getMultimatch="getMultimatchResult($event)"
    ></SearchInput>
    <ul v-if="searchcontent&&!isSearch">
      <SuggestItem
        v-for="(item, index) in searchcontent"
        :key="index"
        :item="item"
        @searching="getKeyword($event)"
      ></SuggestItem>
    </ul>
    <ul class="hotlist" v-if="!searchcontent">
      <h3>热门搜索</h3>
      <HotIist
        v-for="(item, index) in hotlist"
        :key="index"
        :item="item"
        @searchHotkey="searchHotWord($event)"
      ></HotIist>
    </ul>
    <ul v-if="isSearch&&searchcontent">
      <SearchMultimatch :searchmultimatch="searchmultimatch"></SearchMultimatch>
      <SongListItem
        v-for="(item, index) in keywordcontent"
        :key="index"
        :song-item="item"
        :index="index"
        :is-show-quality="true"
        :is-show-ranking="false"
        @translate-currentid="$emit('translate-currentid',$event)"
      ></SongListItem>
    </ul>
    <img v-if="loading" src="../assets/sample.gif" alt />
  </div>
</template>

<script>
import SearchInput from "@/components/SearchInput.vue";
import SuggestItem from "@/components/SuggestItem.vue";
import HotIist from "@/components/HotIist.vue";
import SongListItem from "@/components/SongListItem.vue";
import SearchMultimatch from "@/components/SearchMultimatch.vue";

export default {
  components: {
    SearchInput,
    SuggestItem,
    HotIist,
    SongListItem,
    SearchMultimatch
  },
  data() {
    return {
      searchcontent: null,
      hotlist: null,
      keywordcontent: null,
      isSearch: false,
      loading: false,
      searchmultimatch: []
    };
  },
  methods: {
    getSearchcontent(q) {
      if (q) {
        this.loading = true;
        this.axios
          .get(
            "http://music.kele8.cn/search/suggest?keywords=" +
              q +
              "&type=mobile"
          )
          .then(response => {
            ({ allMatch: this.searchcontent } = response.data.result);
            // console.log(response.data);
          })
          .finally(() => {
            this.loading = false;
          });
      }
    },
    translateText(text) {
      this.searchcontent = text;
      this.getSearchcontent(text);
    },
    getHotList() {
      this.axios.get("http://music.kele8.cn/search/hot").then(response => {
        ({ hots: this.hotlist } = response.data.result);
        // console.log(response.data);
      });
    },
    getKeywordContent(key) {
      this.axios
        .get("http://music.kele8.cn/search?keywords=" + key + "")
        .then(response => {
          ({ songs: this.keywordcontent } = response.data.result);
          // console.log(response.data);
        });
    },
    getSearchMultimatch(q) {
      this.loading = true;
      this.axios
        .get("http://music.kele8.cn/search/multimatch", {
          params: {
            keywords: q
          }
        })
        .then(response => {
          ({ result: this.searchmultimatch } = response.data);
        })
        .finally(() => {
          this.loading = false;
        });
    },
    getKeyword(a) {
      console.log(a);
      this.getKeywordContent(a);
      this.getSearchMultimatch(a);
      this.isSearch = true;
    },
    searchHotWord(hot) {
      console.log(hot);
      this.getKeywordContent(hot);
      this.getMultimatchResult(hot);
      this.isSearch = true;
      this.searchcontent = { hot };
    },
    getMultimatchResult(key) {
      this.getKeywordContent(key);
      this.getSearchMultimatch(key);
      this.isSearch = true;
    },
    Check() {
      if (this.keywordcontent) {
        this.keywordcontent = null;
        this.isSearch = false;
      }
    }
  },
  created() {
    this.getHotList();
  }
};
</script>

<style lang="less" scoped>
ul.hotlist {
  margin: 10px 8px;
  h3 {
    margin: 10px 0;
  }
}

img {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
</style>