<template>
  <div id="app">
    <keep-alive>
      <router-view
        @translate-currentid="currentSongId=$event.songId;showFullscreen=$event.showFullscreen"
      />
    </keep-alive>
    <Player
      v-if="currentSongId"
      :current-song-id="currentSongId"
      :show-fullscreen="showFullscreen"
      @prevsong="prevonesong"
      @nextsong="nextonesong"
    ></Player>
  </div>
</template>


<script>
import Player from "@/views/Player.vue";

export default {
  components: {
    Player
  },
  data() {
    return {
      currentSongId: null,
      showFullscreen: false
    };
  },
  methods: {
    prevonesong() {
      let plist = JSON.parse(window.localStorage.getItem("list"));
      let plistArr = plist.data;
      // console.log(plistArr);
      var i;
      plistArr.forEach(function(ele, index) {
        if (ele.id == this.currentSongId) {
          // console.log(ele, index);
          index--;
          i = index < 0 ? plistArr.length - 1 : index;
        }
      }, this);
      this.currentSongId = plistArr[i].id;
      this.showFullscreen = true;
    },
    nextonesong() {
      let plist = JSON.parse(window.localStorage.getItem("list"));
      let plistArr = plist.data;
      var i;
      plistArr.forEach(function(ele, index) {
        if (ele.id == this.currentSongId) {
          index++;

          i = index > plistArr.length - 1 ? 0 : index;
        }
      }, this);
      this.currentSongId = plistArr[i].id;
      this.showFullscreen = true;
    }
  }
};
</script>
<style lang="less">
#app {
  position: relative;
}
a {
  text-decoration: none;
  color: #2c3e50;
}
.go-back {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 99;
  i {
    color: #eee;
    font-size: 32px;
  }
}
</style>
